"use client";

import { 
  FileJson, 
  FileCode, 
  Folder, 
  FolderOpen, 
  ChevronRight, 
  ChevronDown,
  Settings,
  MoreHorizontal,
  Download,
  Share,
  Play
} from "lucide-react";
import { useState } from "react";
import { Panel, PanelGroup, PanelResizeHandle } from "react-resizable-panels";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/esm/styles/prism";

import { cn } from "~/lib/utils";

// Mock file tree data to match the image
const FILE_TREE = [
  {
    name: "app",
    type: "folder",
    isOpen: false,
    children: []
  },
  {
    name: "components",
    type: "folder",
    isOpen: true,
    children: [
      { name: "use-mobile.ts", type: "file", icon: FileCode, color: "text-blue-400" },
      { name: "use-toast.ts", type: "file", icon: FileCode, color: "text-blue-400" }
    ]
  },
  {
    name: "hooks",
    type: "folder",
    isOpen: false,
    children: []
  },
  {
    name: "lib",
    type: "folder",
    isOpen: false,
    children: []
  },
  {
    name: "public",
    type: "folder",
    isOpen: true,
    children: [
      { name: "components.json", type: "file", active: true, icon: FileJson, color: "text-yellow-400" },
      { name: "next.config.js", type: "file", icon: FileCode, color: "text-yellow-400" },
      { name: "package.json", type: "file", icon: FileJson, color: "text-red-400" },
      { name: "postcss.config.js", type: "file", icon: FileCode, color: "text-yellow-400" }
    ]
  }
];

const MOCK_CODE = `{
  "$schema": "https://ui.shadcn.com/schema.json",
  "style": "new-york",
  "rsc": true,
  "tsx": true,
  "tailwind": {
    "config": "tailwind.config.js",
    "css": "app/globals.css",
    "baseColor": "neutral",
    "cssVariables": true,
    "prefix": ""
  },
  "aliases": {
    "components": "@/components",
    "utils": "@/lib/utils",
    "ui": "@/components/ui",
    "lib": "@/lib",
    "hooks": "@/hooks"
  },
  "iconLibrary": "lucide"
}`;

export function IdeBlock({ className }: { className?: string }) {
  const [files, setFiles] = useState(FILE_TREE);

  const toggleFolder = (name: string) => {
    setFiles(files.map(f => {
      if (f.name === name) {
        return { ...f, isOpen: !f.isOpen };
      }
      return f;
    }));
  };

  return (
    <div className={cn("flex h-full w-full flex-col bg-[#09090b] text-gray-300 border border-white/10 rounded-lg overflow-hidden", className)}>
      {/* Header */}
      <div className="flex h-12 shrink-0 items-center justify-between border-b border-white/10 px-4 bg-[#09090b]">
        <div className="flex items-center gap-2">
          <span className="font-medium text-sm text-white">components.json</span>
        </div>
        <div className="flex items-center gap-2">
           <button className="p-2 hover:bg-white/5 rounded"><Download size={16} /></button>
           <button className="p-2 hover:bg-white/5 rounded"><Share size={16} /></button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-grow overflow-hidden">
        <PanelGroup direction="horizontal">
          {/* File Tree */}
          <Panel defaultSize={20} minSize={15} maxSize={30} className="flex flex-col border-r border-white/10 bg-[#0c0c0e]">
            <div className="p-2">
              <div className="mb-2 px-2 text-xs font-bold text-gray-500 uppercase tracking-wider">Explorer</div>
              {files.map((item) => (
                <div key={item.name}>
                  <div 
                    className="flex cursor-pointer items-center gap-1 rounded px-2 py-1 text-sm hover:bg-white/5"
                    onClick={() => toggleFolder(item.name)}
                  >
                    {item.isOpen ? <ChevronDown size={14} /> : <ChevronRight size={14} />}
                    <Folder size={14} className={item.isOpen ? "text-blue-400" : "text-gray-400"} />
                    <span>{item.name}</span>
                  </div>
                  {item.isOpen && item.children.map((child) => (
                    <div 
                      key={child.name}
                      className={cn(
                        "ml-4 flex cursor-pointer items-center gap-2 rounded px-2 py-1 text-sm hover:bg-white/5",
                        child.active && "bg-white/10 text-white"
                      )}
                    >
                      {child.icon && <child.icon size={14} className={child.color} />}
                      <span>{child.name}</span>
                      {child.name.includes("use") && <span className="ml-auto text-xs text-green-500">+21</span>}
                    </div>
                  ))}
                </div>
              ))}
            </div>
          </Panel>

          <PanelResizeHandle className="w-1 bg-black hover:bg-blue-500/50 transition-colors" />

          {/* Code Editor */}
          <Panel defaultSize={80}>
             <div className="h-full w-full overflow-auto bg-[#1e1e1e]">
               <SyntaxHighlighter
                 language="json"
                 style={vscDarkPlus}
                 customStyle={{ margin: 0, padding: '1.5rem', background: 'transparent', fontSize: '14px', lineHeight: '1.5' }}
                 showLineNumbers={true}
               >
                 {MOCK_CODE}
               </SyntaxHighlighter>
             </div>
          </Panel>
        </PanelGroup>
      </div>
      
      {/* Status Bar */}
      <div className="flex h-6 shrink-0 items-center justify-between border-t border-white/10 bg-[#007acc] px-3 text-xs text-white">
         <div className="flex items-center gap-3">
           <div className="flex items-center gap-1"><Settings size={12} /> master*</div>
           <div>0 errors</div>
         </div>
         <div className="flex items-center gap-3">
           <div>Ln 12, Col 34</div>
           <div>JSON</div>
           <div>UTF-8</div>
         </div>
      </div>
    </div>
  );
}
